// Home Body
const Home = () => {
  const [news, setNews] = useState([]);

  return (
    <>
      <div className="slide slide-first white-text" style={background}>
        <div className="container centered">
          <img
            src={logo}
            alt="mascotte"
            style={{ maxWidth: "400px", margin: "20px" }}
          />
          <h1>Hi everyone!</h1>
        </div>
      </div>

      <div className="slide dotted">
        <div className="container centered">
          <div>
            <AboutSection />
            <Separator height={6} />
            <SkillSection />
            <Separator height={6} />
            <ProjectsSection />
            <Separator height={6} />
            <PublicationSection />
          </div>
        </div>
      </div>
    </>
  );
};

export default Home;
